* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {

    display: flex;
    font-family: 'Poppins', sans-serif;
    background-color: #fff;
    flex-direction: column;
    align-items: center;
}

main {

    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
}

.slider {
    width: 85%;
    height: 500px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    background: white;
    border-radius: 4px;
}

.slideritems {
    width: 23%;
    height: 100%;
    background-position: center;
    background-color: white;
    background-size: cover;
    border-radius: 4px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    align-content: center;
}

.title {
    width: 100%;
    border-radius: 4px;
    height: 100%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    -webkit-transition: all 0.2s 0s ease;
    -moz-transition: all 0.2s 0s ease;
    -o-transition: all 0.2s 0s ease;
    transition: all 0.2s 0s ease;
    text-decoration: 1px solid white;
    background: $turquoise;

}

.slidertitle {
    background: $turquoise;
    width: 100%;
    height: 100%;
    -webkit-transition: all 0.5s 0s linear;
    -moz-transition: all 0.5s 0s linear;
    -o-transition: all 0.5s 0s linear;
    transition: all 0.5s 0s linear;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    align-content: center;
    border-radius: 0 0 4px 4px;

}

.popularpoststitle h3,
.title h2,
.slidertitle h3 {
    -webkit-transition: all 0.1s 0s linear;
    -moz-transition: all 0.1s 0s linear;
    -o-transition: all 0.1s 0s linear;
    transition: all 0.1s 0s linear;
    text-decoration: none;

}

.popularpostsitem:hover h3,
.title:hover h2,
.slidertitle:hover h3 {
    -moz-transform: translate(0px, -11px);
    -webkit-transform: translate(0px, -11px);
    -o-transform: translate(0px, -11px);
    -ms-transform: translate(0px, -11px);
    transform: translate(0px, -11px);
    text-decoration: underline;
}

.popularpoststitle h3,

.slidertitle h3 {

    display: inline-block;
    width: 90%;
    margin-bottom: 20px;
}

.popularpoststitle h3:nth-of-type(4),
.popularpoststitle h3:last-of-type {
    margin-bottom: 0;
}

.popularpoststitle a,
.slidertitle a {
    color: white !important;
}

.outerslider {
    width: 100%;
    background-color: #fff;
    display: flex;
    align-items: center;
    flex-direction: column;

    justify-content: center;
}

.editorspick {
    color: grey;
    width: 85%;
    margin: 10px 0;
}

.posts {


    width: 60%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.post {

    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    background: #ffffff;
    border-radius: 4px;
    margin: 10px 0px;
    height: 280px;
}


.sidebaritem:first-of-type {
    margin-top: 5px;
}

.post:first-of-type {
    margin-top: 0px;
}


.postimage {
    width: 30%;
    background-position: center;
    background-size: cover;
    border-radius: 4px;
    height: 280px;
    margin: 5px 0px;
    -webkit-transition: all 0.5s 0s ease;
    -moz-transition: all 0.5s 0s ease;
    -o-transition: all 0.5s 0s ease;
    transition: all 0.5s 0s ease;
}

.post:hover .postimage {
    border-radius: 0;
}

.posttext {
    width: 100%;
    padding: 0 40px;
    color: grey;
    font-size: large;
    line-height: 25px;

}

.posttitle {

    width: 100%;
    padding: 0 40px;
    margin: 0;
}

::selection {
    background-color: #1b5751;
    color: white;
}

.postcontent {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    width: 70%;
    justify-content: space-between;
}

.title a {
    color: white !important;
}

.title h2,
.title h3 {
    padding: 10px;
    width: 100%;
}

.postauthor {

    color: grey;
    font-size: large;

    width: 100%;
    padding: 0 40px;
    font-family: 'Poppins', sans-serif;
}

nav {
    width: 100%;
    background: white;
    box-shadow: 0px 6px 100px 0px rgb(55 56 64 / 15%);
}

.navbar-brand {
    color: grey;
    background-color: white;
    border-radius: 4px;
    padding: 10px;
}

section {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 20px;

}

.siderbar {

    display: flex;
    flex-direction: column;
    width: 100%;
    align-self: flex-start;
}

.sidebaritem {

    width: 100%;
    height: 200px;
    border-radius: 4px;
    margin: 10px 0;
    background-position: center;
    background-size: cover;

    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.postimage,
.post {
    margin-left: 0;
}


.trending {
    width: 85%;
    height: 300px;
    border-radius: 4px;

    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;

}

.trendingitemcontainer {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
}

.trendingitemcontainer h2 {
    width: 20%;
    padding: 10px;
}

.trendingitem {
    border-radius: 4px;

    width: 80%;
    height: 250px;
    background-position: center;
    background-size: cover;
}

.reply-title,
.comment-content {
    color: grey;
}

.popularposts {

    display: grid;
    width: 85%;
    grid-template-rows: 2fr 1fr;

    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 20px 20px;
    margin-top: 20px;

}

.popularpostsitem {
    width: 100%;
    border-radius: 4px;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.popularpostsitem:first-of-type {
    height: 500px;
    grid-row: 1/-1;
}

.popularpoststitle {
    display: grid;
    width: 100%;
    border-radius: 4px;
    height: 100%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 0.2s 0s ease;
    -moz-transition: all 0.2s 0s ease;
    -o-transition: all 0.2s 0s ease;
    transition: all 0.2s 0s ease;
    text-decoration: 1px solid white;
    background: $turquoise;
}

.popularpostsitem:first-of-type .popularpoststitle {
    height: 50%;
    border-radius: 0 0 4px 4px;
    display: flex;
    align-items: flex-end;
}

.popularpostsitem:nth-of-type(2) .popularpoststitle,
.popularpostsitem:nth-of-type(3) .popularpoststitle {
    height: 100%;
    border-radius: 0 0 4px 4px;
    display: flex;
    align-items: flex-end;
}

.popularpostsitem:nth-of-type(2) .popularpoststitle h3,
.popularpostsitem:nth-of-type(3) .popularpoststitle h3,
.popularpostsitem:first-of-type .popularpoststitle h3 {
    margin-bottom: 20px;

