.wp-block-theblocktheme-container {

    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    align-content: space-between;
    align-items: flex-start;
    margin-top: 20px;

}

.menu-item {
    display: inline-block;
    margin: 0 5px;
}

#menu-main-header-menu,
.menu-main-header-menu-container {
    padding: 0 !important;
    margin: 0 !important;
}

.navbar-brand {
    margin-left: 10px;
}

.navbar {
    z-index: 1022;
}

.container-fluid {
    width: 100%;
    margin-left: 0;
}

.search-form {
    margin-right: 20px;
    display: flex;
}

.search-field {

    color: grey;
    padding: 5px;
}

.singlepostimg {
    background-size: cover;
    background-position: center;
    height: 300px;
    display: flex;
    align-items: center;
}

.singlepostimg h2 {
    display: inline-block;
    color: white;
    padding: 30px;
}

.contentinner p {
    color: grey;
    width: 100%;
    font-size: 18px;
    font-weight: 300;
    line-height: normal;
    font-family: 'Poppins', sans-serif;
}

.prevpostcontainer a {
    font-size: 20px;
    width: 80%;
}

.contentinner {

    display: flex;
    flex-direction: column;
    margin: 20px 0;

}

.mypost {
    width: 60%;
    margin-right: 1%;
}

.mypostimage {
    align-self: center;
    width: 120%;
    margin-bottom: 20px;
    height: 400px;
    object-position: center;
    object-fit: cover;
    border-radius: 4px;
}

.mypost {

    h2,
    h1 {
        color: grey;

        padding-left: 10px;
    }

}

.prevandnextpost {
    align-items: center;

    width: 49%;
    display: flex;
    padding: 40px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.prevandnextpost:first-of-type {
    justify-content: space-between;
}

.prevandnextpost:last-of-type {

    justify-content: space-between;
}

.prevpostcontainer {
    display: flex;
    justify-content: space-between;
}

.relatedposts {

    margin: 1%;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: max-content;
    border: 1px solid #ccc;
    width: 100%;

    min-height: 430px;
    max-height: 430px;
    transition: 0.5s;
}

.relatedposts img {

    object-fit: cover;
    width: 100%;
    height: 300px;
    padding: 20px;
}

.relatedposts h3,
.relatedposts p {

    padding: 0 70px;
    color: grey;
}


.related-posts {
    display: grid;
    grid-template-columns: 48% 48%;
    grid-template-rows: max-content;
    justify-content: space-evenly;
    margin-bottom: 1rem;

}

.rightcloud {

    width: 25%;
    display: flex;
    flex-direction: column;

    h1,
    h2,
    h3,
    h4,
    h5 {
        color: grey;
    }
}

.wp-block-tag-cloud a,
.wp-block-categories li {

    display: inline-block;
