    border-radius: 4px;
}

.rightcloud h3 {
    border-left: 4px solid grey;
    color: grey;
    padding: 5px;
}

.mostpoptitle {
    border: 1px solid #ccc;
    margin: 5px 0;
    border-radius: 4px;
}

.mostpoptitle h4 {

    display: inline-block;
    width: 90%;
    padding: 10px;
}


.sidebaritempopular {

    width: 100%;
    border-radius: 4px;
    margin: 0;
    background-position: center;
    background-size: cover;
    height: 400px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.sidebaritempopular .title {
    border-radius: 4px;

}

hr {
    color: #ccc;
}

.comment-meta .avatar {
    width: 30px;
    height: 30px;
}

.relatedposttitle {

    width: 96%;
    margin-top: 20px;
    color: grey;
}

.comment-form-author,
.comment-form-email,
.comment-form-url {
    width: 33%;
    display: inline-block;
}

.bi-arrow-left-circle,
.bi-arrow-right-circle {

    width: 35px;
    height: 35px;
    color: #ccc;
}

.archivepostscontainer {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    gap: 20px 20px;

    justify-content: center;
    align-items: start;
    justify-items: center;

}

.archiveimage {
    width: 100%;
    object-position: center;
    object-fit: cover;
    border-radius: 4px;
    height: 280px;
    margin: 5px 0px;
    -webkit-transition: all 0.5s 0s ease;
    -moz-transition: all 0.5s 0s ease;
    -o-transition: all 0.5s 0s ease;
    transition: all 0.5s 0s ease;
}



.archivecontent {
    width: 100%;
}

.archivetext {
    width: 100%;
    padding: 0;
    margin-top: 1rem;
    color: grey;
    font-size: large;
    line-height: 25px;

}

.archiveauthor {
    color: grey;
    font-size: large;
    width: 100%;
}

.archivetitle {

    width: 100%;
    padding: 0;
    margin: 0;
}

.archivepost:hover .archiveimage {
    transform: scale3d(1.1, 1.1, 1);

    transform: scale(1.01);
    transition: all 0.85s ease-out;
}

.archivepost {
    height: max-content;
}

.whatsnew {

    width: 100%;
    color: grey;
    margin-bottom: 15px;
}

.archivetop {
    width: 100%;
    margin: 1% 0;
