    align-items: center;
    display: flex;
    justify-content: center;
    background: #083a35;
    padding: 5%;
    color: white;
}

.searchnothingfound {
    width: 100%;

    .alert .search-field {
        width: 100%;
        height: 40px;
        background: white;
        border: 1px solid grey;
        color: grey;
    }

    .alert-info {

        padding: 10%;
        background: white;
        color: grey;
        width: 100%;
        border: none !important;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.wp-block-quote p {

    font-size: 22px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    align-content: center;
    padding: 10px;
    margin: 0;
}

.wp-block-quote {
    font-size: 22px;
    border-left: 2px solid grey;
    margin-bottom: 1rem;
}

.notfoundcontainer {
    flex-direction: column;

}

.searchresultscontainer {
    flex-direction: column;
}

#wpadminbar a {
    color: white !important;
}

.contactuscontainer {
    display: flex;
    justify-content: space-between;
}

.mb-3 {
    margin-bottom: 1rem !important;
    width: 49%;
    display: inline-block;
}

.tagline {

    background: #F7F7F7;
    padding: 30px;
    font-size: 22px !important;
    padding-left: 5px;
}


@media (max-width: 991.98px) {
    .indexpagecontainer {
        flex-wrap: wrap;
    }

    .postcontent,
    .rightcloud,
    .posts,
    .aboutus,
    .subscribe,
    .mypostimage,
    .mypost {
        width: 100%;

    }

    .mypost {
        margin-right: 0;
    }

    .post {
        height: 400px;
        flex-direction: column;
    }

    .postimage {
        width: 100%;
        border-radius: 0;
    }

    .whatsnew {
        padding-left: 20px;
    }

    .wp-block-theblocktheme-footer {
        flex-wrap: wrap;
    }

    .sidebaritempopular,
    .sidebaritem {
        border-radius: 0;
    }

    .popularpostsitem {
        height: 200px;
    }

    .popularpostsitem:nth-of-type(4) .popularpoststitle h3,
    .popularpostsitem:nth-of-type(5) .popularpoststitle h3 {
        margin-bottom: 20px;
    }

    .popularpoststitle h3 {
        align-self: flex-end;
    }

    .outerslider {
        width: 85%;
    }

    .slideritems {
        width: 100%;
        height: 200px;
        margin: 10px 0;
    }

    .slider {
